* {
  box-sizing: border-box;
}
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
body {
  font-family: 'Open Sans';
  font-weight: 300;
}
.bold {
  font-weight: 500;
}
.btn {
  border: none;
  border-radius: 3px;
  color: #fff;
  font-family: 'Open Sans';
  font-weight: 300;
  height: 60px;
  font-size: 1rem;
}
.btn--light {
  color: #295ee6;
  font-weight: 500;
}
.Login {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(180deg, rgba(41,94,230,0.5), rgba(41,94,230,0.8), #295ee6, #295ee6, #295ee6);
  z-index: 100;
}
.Login__flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
  height: 100%;
  margin: 0 auto;
}
.Login__logo {
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Login__facebook {
  background: #3c5b97;
  width: 100%;
}
.Login__google {
  background: #4587f0;
  width: 100%;
  margin-top: 16px;
}
.Login__buttons {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 16px;
}
.Login__demi {
  width: calc(50% - 8px);
  background: #fff;
}
.Login__text {
  color: #fff;
  text-align: center;
  font-size: 0.875rem;
  height: 10%;
  margin-top: 32px;
  line-height: 1.1875rem;
}
